import {ItemOrdemServico} from '../../../../models';
import {StatusOrdemServico} from '../../../../models/StatusOrdemServico';

type TypeValidaItem = {
    descricao: string;
    siglaMetrica: string;
    quantidadeEstimada: string;
    valorUnitarioEstimado: string;
    quantidadeReal: string;
    valorUnitarioReal: string;
};

/**
 * Avalia os valorees dos atributos da {etapa} e retorna mensagem de erro para
 * cada inconsistncia.
 *
 * @param item Enregvel  a ser validado
 */
export const valida = (item: ItemOrdemServico, statusOrdemServico: StatusOrdemServico): TypeValidaItem => {
    let errosInput: TypeValidaItem = {} as TypeValidaItem;

    errosInput.descricao =
        item.descricao == null || item.descricao.trim() == '' ? 'Uma descrio do servio deve ser informada' : '';
    errosInput.siglaMetrica =
        item.siglaMetrica == null || item.siglaMetrica.trim() == '' ? 'A unidade do servio deve ser informada' : '';

    //planejamento
    if (statusOrdemServico == StatusOrdemServico.RASCUNHO) {
        errosInput.quantidadeEstimada =
            item.quantidadeEstimada <= 0 ? 'O quantitavo do servio deve ser informado' : '';
        errosInput.valorUnitarioEstimado =
            item.valorUnitarioEstimado <= 0 ? 'O valor unitrio do servio deve ser informado' : '';
    } else {
        //realizado
        errosInput.quantidadeReal =
            item.quantidadeReal == null ? 'O quantitativo do servio realizado deve ser informado' : '';
        errosInput.valorUnitarioReal =
            item.valorUnitarioReal == null || item.valorUnitarioReal <= 0
                ? 'O valor unitrio do servio deve ser informado'
                : '';
    }
    return errosInput;
};
