import {EtapaOrdemServico} from '../../../../models';
import {validaDataInicioMenorIgualDataFim} from '../../lib/validaDataInicioDataFim';

type TypeValidaEtapa = {
    descricao: string;
    dtInicioPlanejada: string;
    dtFimPlanejada: string;
    dtInicioReal: string;
    dtFimReal: string;
    valorAdiantamentoPlanejado: string;
    valorAdiantamentoReal: string;
};

/**
 * Avalia os valorees dos atributos da {etapa} e retorna mensagem de erro para
 * cada inconsistncia.
 *
 * @param etapa Etapa a ser validada
 */
export const valida = (etapa: EtapaOrdemServico): TypeValidaEtapa => {
    let errosInput: TypeValidaEtapa = {} as TypeValidaEtapa;
    errosInput.descricao =
        etapa.descricao == null || etapa.descricao.trim() == ''
            ? 'Uma descrio da etapa de execuo dos servios deve ser informada'
            : '';

    //planejamento
    errosInput.dtInicioPlanejada =
        etapa.dtInicioPlanejada == null ? 'A data planejada para incio da etapa deve ser informada' : '';
    errosInput.dtFimPlanejada =
        etapa.dtFimPlanejada == null ? 'A data prevista para concluso da etapa deve ser informada' : '';
    errosInput.dtFimPlanejada =
        etapa.dtInicioPlanejada != null &&
        etapa.dtFimPlanejada != null &&
        !validaDataInicioMenorIgualDataFim(etapa.dtInicioPlanejada, etapa.dtFimPlanejada)
            ? 'A data prevista para concluso da etapa dever ser posterior  data de incio'
            : '';

    //realizado
    if (etapa.dtInicioReal != null || etapa.dtFimReal != null) {
        errosInput.dtInicioReal = etapa.dtInicioReal == null ? 'A data de incio da etapa deve ser informado' : '';
        errosInput.dtFimReal =
            etapa.dtInicioReal != null &&
            etapa.dtFimReal != null &&
            !validaDataInicioMenorIgualDataFim(etapa.dtInicioReal, etapa.dtFimReal)
                ? 'A data de concluso da etapa dever ser posterior  data de incio'
                : '';
    }

    return errosInput;
};
